import styled from 'styled-components';


export default styled.div`
	background: #f4f3ed;
	color: #A34100;
    padding: 0.5rem;
    margin-top: 0.5rem;
    cursor: pointer;
    border: 4px solid #222;
    color: #000;


    &:hover {
    	background-color: #222;
    	color: #f6f6ef;
	}
`


